.icon-addon {
	position: relative;
	color: @color_3;
	display: block;
	&:after {
		display: table;
		content: " ";
		clear: both;
	}
	&:before {
		display: table;
		content: " ";
	}
	.glyphicon {
		position: absolute;
		z-index: 2;
		left: 10px;
		font-size: 14px;
		width: 20px;
		margin-left: -2.5px;
		text-align: center;
		padding: 10px 0;
		top: 1px;
	}
	.fa {
		position: absolute;
		z-index: 2;
		left: 10px;
		font-size: 14px;
		width: 20px;
		margin-left: -2.5px;
		text-align: center;
		padding: 10px 0;
		top: 1px;
	}
	.form-control {
		padding-left: 30px;
		float: left;
		font-weight: normal;
		&:focus {
			&+.glyphicon {
				color: @color_5;
			}
			&+.fa {
				color: @color_5;
			}
		}
	}
	&:hover {
		.glyphicon {
			color: @color_5;
		}
		.fa {
			color: @color_5;
		}
	}
}
